main {
  display: block;
  margin: 0 auto;
  /* margin-top: 5em;
  max-width: 50em; */
}


/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  align-items: center;
}

.col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.col-start {
  justify-content: flex-start;
  text-align: left;
}

.col-center {
  justify-content: center;
  text-align: center;
}

.col-end {
  justify-content: flex-end;
  text-align: right;
}


/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 100%;
  /* background: var(--neutral-color); */
  /* background-color: #f7f7f7; */
  width: 95%;
  overflow: hidden;
}
.overlay {
    width: 130%;
    margin-left: -15%;
}

.calendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
}

.month__title {
    display: flex;
    justify-content: space-between;
}
.month__title span {
    width: 30%;
}
.calendar .header .icon {
  cursor: pointer;
  transition: .15s ease-out;
}

.calendar .header .icon:hover {
  transform: scale(1.75);
  transition: .25s ease-out;
  color: var(--main-color);
}

.calendar .header .icon:first-of-type {
  margin-left: 1em;
}

.calendar .header .icon:last-of-type {
  margin-right: 1em;
}

.calendar .days {
  text-transform: uppercase;
  font-weight: 400;
  color: var(--text-color-light);
  font-size: 70%;
  padding: .75em 0;
  border-bottom: 4px solid #e8e8e8;
  width: 30%;
}

.calendar .body .cell {
  position: relative;
  height: 5em;
  border-right: 0px solid var(--border-color);
  overflow: hidden;
  /* cursor: pointer; */
  /* background: var(--neutral-color); */
  transition: 0.25s ease-out;
}



.calendar .body .row {
  border-bottom: 0px solid var(--border-color);
}

.calendar .body .row:last-child {
  border-bottom: none;
}

.calendar .body .cell:last-child {
  border-right: none;
}

.calendar .body .cell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 50px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
    color: #2c2c2c;
  font-weight: 300;
  font-size: 20px !important; 
  border-radius: 50%;
  transition: box-shadow .4s ease, background-color .4s ease, color .3s ease;
}

.calendar .body .cell:hover span{
    box-shadow: 0 0 0 3px rgba(91, 173, 59, 0.10) inset;
    /* background-color: #5bad3b; */
    /* color: #ffffff; */
}
.calendar .body .birthday span {
    background-color: #ffeded;
    box-shadow: 0 0 0 3px #f7f7f7 inset;
}
.calendar .body .disabled span {
  color: #2c2c2c;
  opacity: .2 !important;
  pointer-events: none;
}

.calendar .body .cell .bg {
  font-weight: 700;
  line-height: 1;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -.2em;
  right: -.05em;
  transition: .25s ease-out;
  letter-spacing: -.07em;
}

.calendar .body .cell:hover .bg, .calendar .body .selected .bg  {
  opacity: 0.05;
  transition: .5s ease-in;
}

.calendar .body .col {
  flex-grow: 0;
  flex-basis: calc(100%/7);
  width: calc(100%/7);
  text-align: center;
  height: 50px;
  border-radius: 50%;
}

.calendar .body .selected span{
    background-color: #ffeded !important;
    font-size: 20px !important;
    font-weight: 300 !important;
    font-family: 'Rubik',sans-serif;
    letter-spacing: 0.4px;
    box-shadow: 0 0 0px 4px white inset !important;
    border-radius: 50%;
    color: #2c2c2c !important;
    cursor: pointer;
  }
.days .col-center {
    font-size: 20px;
    font-weight: 300;
    line-height: 1.2;
    letter-spacing: 0.4px;
    color: #646464;
}
.weekend {
    color: #ff0000 !important;
}
.disabled__month {
    opacity: .2;
}
